/**************/
/* ROOT SIZES */
/**************/
:root{
    /* BOTTOM-BAR */
    --bottom-bar-h: 50px;
    /* FULL */
    --full: 100%;
    --full-w: 100vw;
    --full-h: 100vh;
    /* GAP */
    --gap-xxs: 1px;
    --gap-xs: 3px;
    --gap-s: 5px;
    --gap-m: 10px;
    --gap-l: 15px;
    --gap-xl: 20px;
    --gap-xxl: 30px;
    --gap-xxxl: 10vh;
    --gap: 2rem;
    /* IMG */
    --img-xs: 20px;
    --img-s: 30px;
    --img-m: 50px;
    --img-l: 100px;
    --img-xl: 300px;
    --img-xxl: 500px;
    --img-xxxl: 1000px;
    --img-full: 100%;
    --img-full-w: 100vw;
    --img-full-h: 100vh;
}

/***********************/
/* OOCSS-OBJECTS CONFIG*/
/***********************/

/***** OOCSS-BLOCK *****/
    .oocss-block{
        display: inline-grid;
    }
    .oocss-block.oocss-discret{
        gap: 0;
    }
/***********************/

/***** OOCSS-BUTTON *****/
    .oocss-button{
        padding: var(--gap-m);
        width: auto;
    }
        .oocss-button.oocss-interface{
            min-width: calc((2 * (2px + var(--gap-xxs)) + 32px));
            min-height: calc((2 * (2px + var(--gap-xxs)) + 32px));
            padding: var(--gap-xxs);
        }
    
    .oocss-button-rounded{
        width: 50px; min-width: 50px; max-width: 50px;
        height: 50px; min-height: 50px; max-height: 50px;
        margin: var(--gap-m);
        display: flex;
        justify-content: center;
        align-items: center;
    }
/************************/


/***** OOCSS-CARD *****/
.oocss-card{
box-shadow: 0 2px 14px 0 var(--shadow-light);
background-color: var(--white);
border-radius: var(--gap-l);
padding: var(--gap-m);
position: relative;
overflow: hidden;
}
/**********************/

/***** OOCSS-CENTER *****/
    .oocss-center-all{
        margin: auto;
        align-items: center !important;
        justify-content: center !important;
        justify-items: center !important;
    }

    .oocss-center-h{
        margin: 0 auto;
        text-align: center;
        justify-items: center !important;
    }
        .oocss-center-h.oocss-vertical{
            align-items: center;
        }
        .oocss-center-h.oocss-horizontal{
            justify-content: center;
        }

    .oocss-center-v{
        margin: auto 0;
        vertical-align: center;
    }
        .oocss-center-v.oocss-horizontal{
            align-items: center;
        }
        .oocss-center-v.ooccs-vertical{
            justify-content: center;
        }
/************************/

/***** OOCSS-FORM *****/
.oocss-form{
    display: grid;
    grid-row-gap: var(--gap-xxl);
}
    .oocss-form.embedded{
        margin-left: var(--gap-l);
        grid-row-gap: var(--gap-s);
        padding: var(--gap-m);
        /* background-color: var(--smoke); */
        border: 2px solid var(--smoke);
        border-radius: var(--gap-m);
    }
    .oocss-form.embedded:not(:first-child){
        margin-top: var(--gap-xxl);
    }
    .oocss-form__d__message{
        grid-template-columns: 1fr;
        grid-row-gap: 5vh;
    }

    .oocss-form-row{
        display: grid;
        flex-direction: column;
        align-items: start;
        justify-items: start;
        grid-row-gap: var(--gap-s);
    }

.oocss-label{
    display: block;
    /* padding: var(--gap-s); */
}

.oocss-form-text,
.oocss-form-select,
.oocss-form-checkbox,
.oocss-form-textarea{
    padding: var(--gap-l);
}

.oocss-form-text.titre,
.oocss-form-textarea,
.oocss-form-ckeditor,
.cke{
    width: 100%; min-width: 100%; max-width: 100%;
    margin: 0 auto;
}

.oocss-form-textarea{
    resize: vertical;
}

.oocss-form-checkbox{
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    vertical-align: center;
}

.oocss-form-select{
    height: auto; max-height: auto; min-height: auto;
    padding-right: 20px;
    background-image: url('../../mesclics/images/interface/ashes/arrow-down.png');
    background-position: calc(100% - var(--gap-s)) center;
    background-repeat: no-repeat;
    background-size: 15px;

}


.oocss-form-option{
    width: auto; min-width: auto; max-width: auto; min-height: auto; max-height: auto;
    margin: 5px;
}

.oocss-form-select .oocss-form-option{
    padding: var(--gap-m) var(--gap-s);
}

.oocss-form-date{
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-gap: var(--gap-m);
    align-items: center;
}
.oocss-form-date div{
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
}
.oocss-form-date .oocss-form-select{
    margin-right: 5px;
    margin-left: 5px;
}
.oocss-form-fieldset{
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-flow: column dense;
    grid-column-gap: var(--gap-m);
    align-items: start;
}

.oocss-form-input{
    min-width: auto;
    width: auto;
    max-width: none;
    margin: var(--gap-s) 0;
}

.oocss-form-input-button{
    display: none;
}

.oocss-form-input-button + label{
    display: inline-block;
    padding: var(--gap-m);
    margin: var(--gap-xxs);
}

.oocss-filters-button{
    display: inline-block;
    padding: var(--gap-xs) var(--gap-m) var(--gap-xs) var(--gap-xxs);
    margin: var(--gap-xxs);
}


.oocss-form-radio-on-off{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.oocss-form-radio-on-off input{
    width: 0; max-width: 0;
    height: 0; max-height: 0;
    margin: 0;
    opacity: 0; /* preserve keyboard tab && arrow navigation */
}

.oocss-form-radio-on-off label{
    display: inline-block;
    padding: var(--gap-m);
    margin: 0 var(--gap-xs) 0 0;
}

.oocss-form-validate{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: var(--gap-m);
}

/**********************/

/***** OOCSS-FULL-HEIGHT & OOCSS-FULL-WIDTH *****/
    .oocss-full-screen-height{
        height: calc(var(--full-h) - var(--bottom-bar-h));
    }

    .oocss-full-screen-width{
        width: var(--full-w);
    }

    .oocss-full-container-height{
        height: var(--full);
    }

    .oocss-full-container-width{
        width: var(--full);
        min-width: var(--full);
    }
/************************************************/

/***** OOCSS-HEADED-BLOCK *****/
.oocss-headed-block{
    display: inline-grid;
    align-items: center;
    grid-auto-flow: column;
    grid-gap: var(--gap-s);
}
    .oocss-headed-block:before{
        content: ' ';
        display: block;
    }
    .oocss-headed-block.oocss-s:before{
        width: var(--img-s);
        height: var(--img-s);
    }

/****************************/

/***** OOCSS-HORIZONTAL *****/
    .oocss-horizontal{
        grid-auto-flow: column;
        grid-column-gap: var(--gap-s);
        grid-row-gap: var(--gap-m);
        /* grid-template-columns: repeat(auto-fill, 1fr); */
    }
        .oocss-headed-block.oocss-horizontal{
            grid-column-gap: var(--gap-m);
        }
/****************************/

/***** OOCSS-IMAGE *****/
.oocss-image{
    height: auto;
    object-fit: contain;
}
    .oocss-image.oocss-s{
        max-height: var(--img-s);
    }
    .oocss-image.oocss-m{
        max-height: var(--img-m);
    }
    .oocss-image.oocss-l{
        max-height: var(--img-l);
    }
/***********************/

/***** OOCSS-INDEX *****/
.oocss-index{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 16px; max-width: 16px;
    height: 16px; max-height: 30px;
    margin: 3px;    
    overflow-x: hidden;
}
    .oocss-index__d__admin_menu{
        justify-self: flex-end;
        align-self: flex-end;
        transform: translate(-5px, -5px);
    }
/***********************/

/*** OOCSS-JUSTIFY *****/
    .oocss-justify-center{
        justify-items: center;
    }
    .oocss-justify-end{
        justify-items: end;
    }
    .oocss-justify-right{
        justify-self: right;
    }
/***************************/


/***** OOCSS-LEGEND *****/
.oocss-legend{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* margin: 30px auto; */
}
    .oocss-legend.oocss-horizontal{
        flex-direction: row;
        flex-wrap: wrap;
    }

.oocss-legend--item{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 5px;
}
/************************/

/***** OOCSS-LIST ****/
/* .oocss-list{
    display: flex;
}
    .oocss-list__d__column{
        flex-direction: column;
    }

    .oocss-list__d__row{
        flex-direction: row;
    }

.oocss-list--item{
    margin: var(--gap-m);
} */

.oocss-list{
    display: grid;
    grid-row-gap: var(--gap-m);
}
/*********************/

/***** OOCSS-MESSAGE *****/
    .message{
        display: grid;
        grid-template-rows: 1fr 2fr auto;
    }
        .message__d__lateral{
            grid-row-gap: 1vh;
        }

    .message--content{
        padding: var(--gap-m);
    }

    .message--controls{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .message-controls--item{
        display: block;
        padding: 5px;
        margin: 3px;
    }
    /* OOCSS-MESSAGE--HEADERS */
    /* .message--headers,
    .message--content{
        padding-bottom: 3vh;
    } */
/*************************/

/***** OOCSS-SPACE-AROUND *****/
    .oocss-space-around{
        justify-content: space-around;
    }
/*******************************/

/***** OOCSS-SPACE-BETWEEN *****/
    .oocss-space-between{
        justify-content: space-between;
    }
/*******************************/


/***** OOCSS-SUB_SECTION *****/
.oocss-sub_section-nav--bloc{
    margin-bottom: 15px;
}

.oocss-sub_section-nav-bloc--head{
    margin-bottom: 15px;
}

.oocss-sub_section-nav--item{
    padding: 5px;
}
.oocss-sub_section-nav--item:not(:first-child){
    margin-left: 10px;
}

.oocss-sub_section-nav--item:before{
    margin: 5px;
}
/*****************************/

/***** OOCSS-TABLES *****/
.oocss-table{
    display: grid;
    row-gap: var(--gap-m);
}
    .oocss-table.oocss-horizontal{
        grid-auto-flow: column;
        row-gap: 0;
        column-gap: var(--gap-m);
    }

.oocss-table-row,
.oocss-table-head-row{
    display: inline-grid;
    grid-template-columns: var(--cols-template);
    column-gap: var(--gap-m);
    padding: var(--gap-xs);
}

    .oocss-horizontal .oocss-table-row,
    .oocss-horizontal .oocss-table-head-row{
        grid-auto-flow: row;
        grid-template-columns: minmax(150px, 1fr);
    }

.oocss-table-data,
.oocss-table-head{
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-self: center;
    text-align: center;
}

    .oocss-table-data__d__post{
        padding: 0;
    }
    .oocss-table-data.oocss-horizontal{
        flex-direction: row;
        align-items: center;
    }

.oocss-table-col-sort.oocss-active:after{
    content: ' ';
    display: inline-block;
    margin-left: 3px;
}
    .oocss-table-col-sort.oocss-active.sort-ASC:after{
        content: '(a-z)';
        /* content: '↑'; */
    }
    .oocss-table-col-sort.oocss-active.sort-DESC:after{
        content: '(z-a)';
        /* content: '↓'; */
    }
/************************/

/***** OOCSS-TITLES *****/
/************************/

/***** OOCSS-VERTICAL *****/
    .oocss-vertical{
        flex-direction: row;
        grid-auto-flow: row;
        grid-auto-columns: minmax(0, auto);
        grid-column-gap: var(--gap-s);
        grid-row-gap: var(--gap-m);
    }
/**************************/

/***** OOCSS-WIDGET *****/

.oocss-widget{
    box-sizing: content-box;
    width: 100%;
    overflow-x: auto;
}
    .oocss-widget-closed{
        overflow: hidden;
    }
    .oocss-widget-closed .oocss-widget--content{
        display: none;
    }

.oocss-widget--content{
    padding: var(--gap-xl);
    width: auto;
    height: auto;
    transition: all var(--transitions-duration, 800ms) ease-in-out;
    transition: padding 0ms;
    display: grid;
    grid-row-gap: var(--gap-xl);
    grid-column-gap: var(--gap-m);
    grid-auto-columns: minmax(auto, 100%);
}

.oocss-widget-closed .oocss-widget--content{
    height: 0;
    padding: 0;
}

.oocss-widget--content-zone{
    padding: var(--gap-m);
    margin-bottom: var(--gap-xl);
    margin-top: var(--gap-xl);
    /* display: grid; */
}

.oocss-widget-closed .oocss-widget--content-zone{
    margin-top : 0;
}


.oocss-widget.small{
    grid-column: span 1;
}

.oocss-widget.medium{
    grid-column: span 2;
}

    @media screen and (max-width: 599px){
        .oocss-widget.medium{
            grid-column: span 1;
        }
    }

.oocss-widget.large{
    grid-column: span 3;
}
    @media screen and (min-width: 720px){
        .oocss-widget.large{
            min-width: 650px;
        }
    }

    @media screen and (max-width: 899px){
        .oocss-widget.large{
            grid-column: span 2;
        }
    }

    @media screen and (max-width: 599px){
        .oocss-widget.large{
            grid-column: span 1;
        }
    }

.oocss-widget--head::before{
    content: ' ';
    display: inline-block;
    width: 30px;
    height: 30px;
    background-size: 30px 30px;
    background-repeat: no-repeat;
}

.oocss-widget--head .oocss-widget-openclose{
    display: inline-block;
    width: 30px;
    height: 30px;
    background-size: 15px 15px;
    background-position: center center;
    background-repeat: no-repeat;
}

.oocss-widget-closed .oocss-widget-openclose{
    background-image: url('../../mesclics/images/interface/white/arrow-down.png');
}

.oocss-widget-opened .oocss-widget-openclose{
    background-image: url('../../mesclics/images/interface/white/cross.png');
}

.oocss-widget.not-closable .oocss-widget-openclose{
    display: none;
}

.oocss-widgets-collection-columns{
    display: grid;
    grid-gap: var(--gap-xxxl);
    grid-auto-columns: minmax(300px, auto);
    grid-auto-flow: column dense;
}

.oocss-widgets-collection-rows{
    display: grid;
    grid-gap: var(--gap-xxxl);
    grid-auto-rows: minmax(30px, auto);
    grid-auto-flow: row dense;
}
/************************/

/****************/
/* OOCSS STYLES */
/****************/

/***** OOCSS-DISCRET *****/
        .oocss-discret,
        [class*="oocss"].oocss-discret{
            background-color: transparent;
        }
/*************************/

/***** OOCSS-BUTTON *****/
    .oocss-button{
        background-color: var(--red);
        color: var(--white);
        transition: background-color 150ms ease-out;
        border: 2px solid transparent;
    }
        .oocss-button.oocss-inline{
            border: 0px;
            background-color: var(--ashes);
            color: var(--white);
            font-size: var(--small-text);
        }
        .oocss-button:not(.oocss-inline):hover{
            background-color: var(--hard-red);
        }
            .oocss-button:not(.oocss-headed-block).oocss-discret{
                border-color: var(--smoke);
                color: var(--grey);
            }
            .oocss-button.oocss-button-rounded.oocss-discret{
                color: var(--white);
            }
            .oocss-button:not(.oocss-headed-block).oocss-very-discret{
                border-color: transparent;
            }
            .alert .oocss-button:not(.oocss-headed-block).oocss-discret{
                opacity: 0.7;
            }
            .oocss-button.oocss-discret:hover{
                background-color: transparent;
                transform: translateY(-3px);
                transition: all ease-in-out 300ms;
            }
            .popup-in:not(.alert) .oocss-button.oocss-discret:hover{
                color: var(--red);
            }
            .oocss-button:not(.oocss-headed-block).oocss-discret:hover{
                border-color: var(--red);
            }
            .alert .oocss-button:not(.oocss-headed-block).oocss-discret:hover{
                border-color: var(--smoke);
                opacity: 1;
                transform: translateY(0);
            }
            
    .oocss-button-rounded{
        border-radius: 50%;
        color: var(--smoke);
    }
        .oocss-button-rounded.oocss-discret{
            background-color: var(--grey);
        }
        .oocss-button-rounded.oocss-discret:hover{
            background-color: var(--red);
            color: var(--smoke);
            transform: none;
        }

        
/***********************/

/***** OOCSS-FILTER *****/
        .oocss-filterable.oocss-filtered-out{
            display: none;
        }
/************************/


/***** OOCSS-FORM *****/
    .oocss-form-input-button + label,
    .oocss-form-radio-on-off label,
    .oocss-filters-button{
        border: solid 2px var(--smoke);
        color: var(--grey);
        transition: all 150ms ease-in-out;
    }

    
    .oocss-form-input-button:not(:last-child) + label,
    .oocss-form-radio-on-off:not(:last-child) label,
    .oocss-filters-button:not(:last-child){
       margin-right: var(--gap-s);
    }

    .oocss-form-radio-on-off input:checked + label,
    .oocss-form-input-button:checked + label,
    .oocss-form-input-button:valid,
    .oocss-form-input-button:focus,
    .oocss-filters-button.active{
        border-style: solid;
        border-color: var(--red);
        color: var(--red);
    }

    .oocss-form-error{
        color: var(--red);
    }
/**********************/

/***** OOCSS-HEADED-BLOCK *****/
    .oocss-widget .oocss-headed-block{
        color: var(--smoke);
    }

    .oocss-widget:not(.oocss-discret) .oocss-headed-block{
        /* color: var(--ashes); */
    }
    .oocss-widget:not(.oocss-discret) .oocss-headed-block.oocss-form-submit{
        color: var(--smoke);
    }
    .oocss-widget:not(.oocss-discret) .oocss-headed-block.oocss-discret{
        color: var(--grey);
        border: 2px solid var(--grey);
    }

    .oocss-headed-block:before{
        background-size: contain;
        background-repeat: no-repeat;
    }
/*****************************/

/***** OOCSS-INDEX *****/
    .oocss-index{
        background-color: black;
        color: var(--smoke);
        border-radius: 50%;
        margin: 7px 5px;
    }
        .oocss-index.isProspect,
        .oocss-index.isClient,
        .oocss-index.publicPost,
        .oocss-index.privatePost{
            border-radius:0;
            background-size: 30px 30px;
            background-color: transparent;
            min-width: 30px; max-width: 30px;
            height: 30px;
            margin: 0 3px;
        }
        .oocss-index.isProspect,
        .oocss-index.isClient{
            border-radius:0;
            background-size: 32px 32px;
            background-position: 0px;
            background-color: transparent;
            min-width: 32px; max-width: 32px;
            height: 32px; max-height: 32px;
        }

        .oocss-index.oocss-highlight,
        .oocss-index__d__admin_menu,
        .oocss-index__d__unread_messages{
            background-color: var(--red);
            color: var(--smoke);
        }
        /**CLIENTS**/
        .oocss-index.isProspect{
            background-color: transparent;
            background-image:url('../../mesclicsespaceclient/images/icones/clients/ashes/prospect.png');
        }
        .oocss-filters-button.active .oocss-index.isProspect{
            background-image:url('../../mesclicsespaceclient/images/icones/clients/red/prospect.png');
        }
        .oocss-index.isClient{
            background-color: transparent;
            background-image:url('../../mesclicsespaceclient/images/icones/clients/ashes/client.png');
        }
        .oocss-filters-button.active .oocss-index.isClient{
            background-image:url('../../mesclicsespaceclient/images/icones/clients/red/client.png');
        }
        /**POSTS**/
        .oocss-index.allPost{
            background-image: url('../../mesclicspost/images/icones/ashes/publication.png');
        }
        .oocss-index.onlinePost{
            background-color: var(--red);
        }
        .oocss-index.onlineWithPeremptionPost{
            background-color: transparent;
            border: 5px solid var(--red);
        }
        .oocss-index.toBePublishedPost{
            background-color: transparent;
            border: 2px solid var(--red);
        }
        .oocss-index.unpublishedPost{
            background-color: transparent;
            border: 2px solid var(--ashes);
        }
        .oocss-index.draftPost{
            background-color: var(--ashes);
        }
        .oocss-index.publicPost{
            background-image: url('../../mesclicspost/images/icones/ashes/public.png');
        }
        .oocss-index.privatePost{
            background-image: url('../../mesclicspost/images/icones/ashes/private.png');
        }
/***********************/

/***** OOCSS-INSTRUCTION *****/
        .oocss-instruction.oocss-discret{
            color: var(--grey);
        }
/*****************************/

/***** OOCSS-SLIDESHOW *****/

.oocss-slideshow{
  display: grid;
  grid-template-columns: 100%;
  width: var(--oocss-slideshow-width, 100%);
  max-width: var(--oocss-slideshow-width, 100%);
  margin: 0 auto;
  overflow-x: hidden;
}

.oocss-slideshow--content{
  display: grid;
  transition: transform var(--oocss-slideshow-slide-transition-duration, 300ms) ease-in-out;
  min-height: var(--oocss-slideshow-height, auto);
  max-height: var(--oocss-slideshow-height, auto);
}

.oocss-slideshow--slider{
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  height: var(--oocss-slideshow-slider-height, auto);
  width: var(--oocss-slideshow-slider-width, 100%);
}

.oocss-slideshow--cursors{
  display: flex;
  flex-direction: columns;
  justify-content: center;
  align-items: center;
  grid-column: 2;
}

.oocss-slideshow--cursor{
  padding: 0;
  border: none;
  box-shadow: none;
  
  display: block;
  outline: none;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  
  background-color: var(--oocss-slideshow-slider-cursors-color, grey);
  margin: 3px;
  cursor: pointer;
  transition: all var(--oocss-slideshow-slider-transition-duration, 300ms) ease-in-out;
}

.oocss-slideshow--cursor.active{
  background-color: var(--oocss-slideshow-slider-cursor-active-color, red);
}

.oocss-slideshow--prev{
  border: 0;
  background-color: transparent;
  color: var(--oocss-slideshow-slider-nav-color, inherited);
  grid-column: 1;
  cursor: pointer;
}

.oocss-slideshow--next{
  border: 0;
  background-color: transparent;
  color: var(--oocss-slideshow-slider-nav-color, inherited);
  grid-column: 3;
  cursor: pointer;
}
