/***** OOCSS-HAMBURGER-ICON *****/
    .oocss-hamburger-icon{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: var(--oocss-hamburger-icon--width, 30px);
    height: var(--oocss-hamburger-icon--width, 30px);
    background-color: transparent;
    padding: 0;
    outline: 0;
    border: 0;
    cursor: pointer;
    }

    .oocss-hamburger-icon_top,
    .oocss-hamburger-icon_middle,
    .oocss-hamburger-icon_bottom{
    --height: calc(var(--oocss-hamburger-icon--weight, 3px));
    --width: var(--oocss-hamburger-icon--width, 30px);
    --between: calc((var(--width) - 3 * var(--height)) / 2);
    display: block;
    box-sizing: border-box;
    background-color:  var(--oocss-hamburger-icon--color, black);
    min-width: var(--width);
    width: var(--width);
    min-height: var(--height);
    border-radius: var(--oocss-hamburger-icon--radius, 5px);
    animation-duration: var(--oocss-hamburger-icon--animation-duration, 300ms);
    animation-timing-function: var(--oocss-hamburger-icon--animation-transition, ease-in-out);
    animation-fill-mode: forwards;
    transform-origin: 50% 50%;
    }

    .hamburgerIconToCloseIcon.opened .oocss-hamburger-icon_top{
    animation-name: hamburgerIconToCloseIcon_top ;
    }
    .hamburgerIconToCloseIcon.closed .oocss-hamburger-icon_top{
    animation-name: closeIconToHamburgerIcon_top;
    }
    @keyframes hamburgerIconToCloseIcon_top{  
    100%{ transform:  translateY(calc(var(--height) + var(--between))) rotate(45deg); }
    }
    @keyframes closeIconToHamburgerIcon_top{
    0%{ transform:  translateY(calc(var(--height) + var(--between))) rotate(45deg); }
    100%{ transform:  translateY(0) rotate(0); }
    }

    .hamburgerIconToCloseIcon.opened .oocss-hamburger-icon_bottom{
    animation-name: hamburgerIconToCloseIcon_bottom;
    }
    .hamburgerIconToCloseIcon.closed .oocss-hamburger-icon_bottom{
    animation-name: closeIconToHamburgerIcon_bottom;
    }
    @keyframes hamburgerIconToCloseIcon_bottom{
    100%{ transform:  translateY(calc(-1 * (var(--height) + var(--between)))) rotate(-45deg);}
    }
    @keyframes closeIconToHamburgerIcon_bottom{
    0%{ transform:  translateY(calc(-1 * (var(--height) + var(--between)))) rotate(-45deg); }
    100%{ transform:  translateY(0) rotate(0); }
    }

    .hamburgerIconToCloseIcon.opened .oocss-hamburger-icon_middle{
    animation-name: hamburgerIconToCloseIcon_middle;
    }
    .hamburgerIconToCloseIcon.closed .oocss-hamburger-icon_middle{
    animation-name: closeIconToHamburgerIcon_middle;
    }
    @keyframes hamburgerIconToCloseIcon_middle{
    100% { transform: scaleX(0); }
    }
    @keyframes closeIconToHamburgerIcon_middle{
    0%{  transform: scaleX(0); }
    100%{  transform: scaleX(1); }
    }
