
:root{
    --oocss-slideshow-slider-cursors-color: var(--ashes);
    --oocss-slideshow-slider-cursor-active-color: var(--red);
}
/***** MESSAGE *****/
    /* MESSAGE--HEADERS */
    .message--headers{
        padding-bottom: 3vh;
        border-bottom: dotted 2px var(--ashes);
    }
        .message--headers__d__lateral{
            border-color: var(--ashes);
        }

    .message--content__d__lateral{
        background-color: var(--smoke);
        color: var(--ashes);
    }

    .message-controls--item{
        background-color: var(--smoke);
    }
        .message-controls--item__d__lateral,
        .message-controls--item__d__lateral:visited{
            color: var(--smoke);
            background-color: var(--ashes);
        }

    .message-controls--item:hover{
        background-color: var(--ashes);
        color: var(--smoke);
    }
        .message-controls--item__d__lateral:hover{
            background-color: black;
        }
