/* WIGETS */
.oocss-widget{
    box-shadow: 0px 5px 40px 0px var(--shadow);
    background-color: var(--black);
    color: var(--white);
}
    .oocss-widget.highlight{
        background-color: var(--red);
        color: var(--smoke);
    }
    .oocss-widget.highlight2{
        background-color: var(--yellow);
        color: var(--smoke);
    }
        .oocss-widget.highlight2 .oocss-button{
            background-color: var(--yellow);
        }
    .oocss-widget.oocss-discret{
        background-color: transparent;
        color: var(--red);
        box-shadow: none;
    }

.oocss-widget--content{
    background-color: var(--white);
    color: var(--ashes);
    cursor: var(--cursor-auto-grey);
}
.oocss-discret .oocss-widget--content{
    background-color: transparent;
    color: var(--smoke);
}
.oocss-widget--content-zone{
