/***** COMMENTS *****/
.comments .oocss-child{
    margin-left: var(--gap-m);
    padding-left: var(--gap-s);
}

.comment .oocss-pinned{
    /* background-color: var(--yellow); */
    padding: var(--gap-m);
    border: var(--gap-xxs) solid var(--yellow);
    border-radius: var(--gap-m);
}

.comment-management .oocss-button.oocss-interface{
    mask-size: cover;
    -webkit-mask-size: cover;
    background-color: var(--grey);
}


.comment-management .oocss-button.oocss-interface:hover{
