/**************/
/* ANIMATIONS */
/**************/

.bottom_bar-admin_menu--item:hover .admin_menu_item-label{
    animation: bouncing-label 300ms ease-in-out 150ms forwards;
}
    @keyframes bouncing-label{
        50%{z-index: -1;}
        75%{transform: translateY(-40px); z-index: 2;}
        100%{transform: translateY(-35px); z-index: 2;}
    }


.flash-messages{
    animation : flashMessages 500ms ease-in-out 150ms forwards;
    overflow: hidden;
}
.flash-message.oocss-closed{
    min-height: 0;
    height: 0;
    transition: min-height 250ms linear 150ms, height 500ms linear 150ms;
}
    @keyframes flashMessages{
        75%{transform: translateY(calc(var(--bottom-bar-h) + 105%));}
        100%{transform: translateY(calc(-1 * ( var(--bottom-bar-h) + 100%)));}
    }
.oocss-index__d__admin_menu.messages{
    animation: index-hey 1000ms ease-in-out infinite;
}
    @keyframes index-hey{
        50%{ transform: scale(1.075) translate(-5px, -5px);}
    }

.message-form__d__new__s__sent{
    animation: fly-away 1000ms cubic-bezier(1, 0, 1, 0.55) forwards;
}

    @keyframes fly-away{
        30%, 60%{ transform: translateY(30vh);}
        100%{ transform: scale(0.8) rotate(40deg) translateY(-300vh) translateX(50vw);}
    }


    .popup-in .popup-illustration svg{
        width: 50%;
        height: auto;
    }